function sliderMain() {
    var swiper = new Swiper('.main .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 15,
        loop: true,
        effect: "fade",
        autoplay: {
            delay: 5000,
            disableOnInteraction: false
        },
        navigation: {
            nextEl: ".main .swiper-button-next",
            prevEl: ".main .swiper-button-prev",
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true
        },
    })
}

function sliderNews() {
    var swiper = new Swiper('.news .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: ".news .swiper-button-next",
            prevEl: ".news .swiper-button-prev",
        },
        pagination: {
            el: ".news .swiper-pagination",
            clickable: true
        },
        breakpoints: {
            320: {
                slidesPerView: 1.3,
                spaceBetween: 20,
            },
            501: {
                slidesPerView: 2.1,
                spaceBetween: 20,
            },
            577: {
                slidesPerView: "auto",
                spaceBetween: 20,
            },
            1601: {
                slidesPerView: "auto",
                spaceBetween: 30,
            }
        }
    })
}

function sliderPartners() {
    var swiper = new Swiper('.partners .mySwiper', {
        slidesPerView: 5,
        spaceBetween: 30,
        loop: true,
        grid: {
            rows: 1,
        },
        navigation: {
            nextEl: ".partners .swiper-button-next",
            prevEl: ".partners .swiper-button-prev",
        },
        pagination: {
            el: ".partners .swiper-pagination",
            clickable: true
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 15,
                loop: false,
                grid: {
                    rows: 2,
                },
            },
            577: {
                slidesPerView: 3,
                spaceBetween: 15,
                loop: false,
                grid: {
                    rows: 2,
                },
            },
            993: {
                slidesPerView: 5,
                spaceBetween: 30,
                loop: true,
                grid: {
                    rows: 1,
                },
            }
        }
    })
}

function gallery() {
    var e = new Swiper(".gallery .mySwiper",{
        slidesPerView: 4,
        spaceBetween: 10,
        freeMode: !0,
        watchSlidesProgress: !0,
        direction: "horizontal",
        grid: {
            rows: 1,
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                grid: {
                    rows: 2,
                },
            },
            577: {
                slidesPerView: 4,
                grid: {
                    rows: 1,
                },
            }
        }
    });
    new Swiper(".gallery .mySwiper2",{
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: e
        },
    })
}

$(document).ready(function () {
    sliderMain();
    sliderNews();
    sliderPartners();
    gallery();

    //Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-mob').toggleClass('header-mob--active')
        $('.menu-bg').toggleClass('menu-bg--active')
        $('body').toggleClass('fixed-body')
    })

    //Up
    var t = $(".up");
    function i() {
        $(".modal").removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }
    $(window).scroll((function() {
        $(window).scrollTop() > 300 ? t.addClass("up--active") : t.removeClass("up--active")
    }
    ))
    t.on("click", (function(e) {
         e.preventDefault(),
            $("html, body").animate({
                scrollTop: 0
            }, "300")
         }
    ))

    //Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active")
        $(this).siblings(".item-hidden").slideToggle()
    }));

    //Scroll
    $('.scroll-up').click(function() {
        var $container = $(this).siblings('.scroll-container');
        $container.animate({
            scrollTop: Math.max($container.scrollTop() - 50, 0)
        }, 300);
    });

    $('.scroll-down').click(function() {
        var $container = $(this).siblings('.scroll-container');
        var maxScroll = $container[0].scrollHeight - $container.innerHeight();
        $container.animate({
            scrollTop: Math.min($container.scrollTop() + 50, maxScroll)
        }, 300);
    });

    //
    function updateScrollButtons($container, $btnLeft, $btnRight) {
        var maxScroll = $container[0].scrollWidth - $container.innerWidth();
        var currentScroll = $container.scrollLeft();

        if (currentScroll <= 0) {
            $btnLeft.addClass('disabled-left');
        } else {
            $btnLeft.removeClass('disabled-left');
        }

        if (currentScroll >= maxScroll) {
            $btnRight.addClass('disabled-right');
        } else {
            $btnRight.removeClass('disabled-right');
        }
    }

    $('.scroll-left').click(function() {
        var $container = $(this).siblings('.scroll-container');
        var $btnLeft = $(this);
        var $btnRight = $btnLeft.siblings('.scroll-right');

        var currentScroll = $container.scrollLeft();
        var newScroll = Math.max(currentScroll - 100, 0);
        $container.animate({ scrollLeft: newScroll }, 300, function() {
            updateScrollButtons($container, $btnLeft, $btnRight);
        });
    });

    $('.scroll-right').click(function() {
        var $container = $(this).siblings('.scroll-container');
        var $btnRight = $(this);
        var $btnLeft = $btnRight.siblings('.scroll-left');

        var maxScroll = $container[0].scrollWidth - $container.innerWidth();
        var currentScroll = $container.scrollLeft();
        var newScroll = Math.min(currentScroll + 100, maxScroll);
        $container.animate({ scrollLeft: newScroll }, 300, function() {
            updateScrollButtons($container, $btnLeft, $btnRight);
        });
    });

    $('.scroll-container').each(function() {
        var $container = $(this);
        var $btnLeft = $container.siblings('.scroll-left');
        var $btnRight = $container.siblings('.scroll-right');
        updateScrollButtons($container, $btnLeft, $btnRight);
    });

    $('.scroll-container').on('scroll', function() {
        var $container = $(this);
        var $btnLeft = $container.siblings('.scroll-left');
        var $btnRight = $container.siblings('.scroll-right');
        updateScrollButtons($container, $btnLeft, $btnRight);
    });

    //Select
    $(".custom_select select").niceSelect(),

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})